<template>
  <div>
    <FormProvider :form="form">
      <SchemaField :schema="schema" />
    </FormProvider>
    <button @click="toCode">toCode</button>
    <div>{{ code }}</div>
  </div>
</template>

<script>
import { Input, Checkbox } from 'ant-design-vue'
import { createForm } from '@formily/core'
import { FormProvider, createSchemaField } from '@formily/vue'
import 'ant-design-vue/dist/antd.css'
import { vueDsl } from '../../utils/vueDsl.js'
// var xtpl = require('xtpl')
// xtpl.renderFile(
//   './vueTemplate.xtpl',
//   {
//     x: 1
//   },
//   function (error, content) {
//     console.log(error, content)
//   }
// )
const { SchemaField } = createSchemaField({
  components: {
    Input,
    Checkbox
  }
})
export default {
  components: { FormProvider, SchemaField },
  data() {
    return {
      form: createForm(),
      schema: {
        type: 'object',
        properties: {
          input: {
            type: 'string',
            'x-component': 'Input',
            'x-component-props': {
              placeholder: ''
            }
          },
          single: {
            type: 'boolean',
            title: '',
            'x-component': 'Checkbox'
          }
        }
      },
      code: '',
      xtpl: require('xtpl')
    }
  },
  methods: {
    toCode() {
      // this.xtpl.renderFile(
      //   './vueTemplate.xtpl',
      //   {
      //     x: 1
      //   },
      //   function (error, content) {
      //     debugger
      //     console.log(error, content)
      //   }
      // )
      this.code = vueDsl(this.schema)
    }
  }
}
</script>